<template>
  <div class="one">
    <mo-xing>
      <div slot="top">项目经历</div>
      <div slot="centor">
        <div>
          《博育组卷系统》
        </div>
        <ul>
          <li>项目描述<br>
            教师在此平台进行新增题目、管理题目、添加试卷、管理试卷列表、在线组卷、发布考试、个人中心修改等的多种功能，并且附加自动获得学员考试成绩情况分析统计图，有效地减少教师传统方式出题出卷所花时间，科学合理地减少冗重的工作量，从而提高教师的工作效率。
            </li>
          <li>责任描述<br>
            主要负责前端的开发，与后端协同优化接口，获取数据。以及后期进行项目的测试。
          </li>
          <li>主要技术<br>
            通过html将页面的基本结构搭建出来，再配合element-ui组件的使用，尽可能发挥组件的最大作用，减少代码的复用，提高性能，最后通过CSS对页面内容进行位置进行调整，将ui设计图最大程度的复现出来。页面采用了flex布局，页面的自适应度大大提高，灵活适应各种大小的屏幕。
            数据请求使用axios封装成模块，供给各个组件使用，方便后期维护。
          </li>
          
        </ul>
      </div>
    </mo-xing>
  </div>
</template>

<script>
  import MoXing from '@/components/MoXing'
export default {
  name: 'XiangMu',
  components:{
    MoXing
  },
  data () {
    return {   
    }
  }
}
</script>


<style scoped>
ul li{
  margin-top: 8px;
}
</style>
  


